*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: monospace;
}

html{
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  height: auto;
  overflow-x: hidden;
  background: rgba(0,0,0);
}

#root{
  width: 100%;
  min-height: 100vh;
  height: auto;
  padding: 50px;
}

header{
  width: 100%;
  height: 50px;
  background: orangered;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  margin-bottom: 25px;
}

header .logo{
  width: 50%;
  text-align: left;
}

header .logo a{
  text-decoration: none;
  color: white;
  font-size: 2rem;
  font-weight: bolder;
}

header form{
  width: 50%;
  text-align: right;
  height: 75%;
}

header form input{
  width: 50%;
  height: 100%;
  outline: 0;
  border: 1px solid black;
  font-size: 16px;
  padding: 5px 15px;
  border-radius: 50px;
  transition: box-shadow .2s linear;
}

header form input:focus{
  box-shadow: 3px 3px 5px 2px rgba(0,0,0,0.5);
}

.movie-container{
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
}

.movie-cover{
  width: 25%;
  display: flex;
  padding: 25px 15px;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.movie{
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  border: 2px solid orangered;
  border-radius: 5px;
  overflow: hidden;
}

.movie img{
  max-width: 100%;
  transition: transform .3s ease;
}

.movie:hover img{
  transform: scale(1.2);
}

.movie span{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
  width: 75%;
  background: orangered;
  color: white;
  font-size: 18px;
  padding: 6px 8px;
  font-weight: bolder;
  opacity: 0;
  transition: all .3s ease;
}

.movie:hover span{
  opacity: 1;
}

.movieInfoContainer{
  width: 80%;
  height: auto;
  display: flex;
  flex-direction: row;
  padding: 25px;
  border: 1px solid white;
  margin: 0 auto;
}

.img{
  width: 30%;
  height: 100%;
  height: auto;
  margin-right: 25px;
}

.img img{
  height: 100%;
  width: 100%;
}

.info{
  font-family: monospace;
  width: 70%;
  text-align: center;
  color: #fff;
}

.info h2{
  color: orangered;
  font-size: 3rem;
  margin-bottom: 15px;
}

.icon{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
}

.info .date{
  font-size: 16px;
}

.icon i{
  margin-right: 10px;
}

.fa-star{
  color: yellow;
}

.infoText{
  font-size: 16px;
}

.transition{
  width: 100%;
  height: auto;
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.transition div{
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  background-color: orangered;
  margin: 10px;
  font-size: 20px;
  cursor: pointer;
  font-weight: bolder;
  border-radius: 50%;
  transition: transform .3s ease;
}

.transition div:hover{
  transform: scale(1.2);
}

.transition div#active{
  background: white;
}

.notfound{
  width: 80%;
  margin: 0 auto;
  color: white;
  text-align: center;
}

.notfound hr{
  margin-top: 10px;
}

@media only screen and (min-width: 1400px) {
  .movie-cover{
    width: 20%;
    padding: 50px;
  }
  .movie-cover .movie span{
    height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
  }
  header{
    height: 100px;
  }
  header .logo a{
    font-size: 4rem;
  }
  header form input{
    width: 75%;
    font-size: 25px;
  }
  .info h2{
    font-size: 5rem;
  }
  .info .icon{
    font-size: 3rem;
  }
  .info .icon p{
    font-size: 3rem;
  }
  .info .infoText{
    font-size: 2rem;
  }
}

@media only screen and (max-width: 750px) {
  .movie-cover{
    width: 33.3333%;
  }
  .info h2{
    font-size: 2rem;
  }
  header form input{
    width: 100%;
  }
}

@media only screen and (max-width: 550px) {
  .movie-cover{
    width: 50%;
  }

  header{
    height: auto;
    padding: 10px;
  }
  header ul{
    flex-direction: column;
  }
  header ul a{
    margin-bottom: 8px;
  }
  header ul li{
    width: 100%;
  }
  header ul .search{
    width: 100%;
  }
  .movieInfoContainer{
    flex-direction: column;
  }
  .movieInfoContainer .img{
    width: 50%;
    margin: 10px auto;
  }
  .movieInfoContainer .info{
    width: 100%;
  }
  .infoText{
    font-size: 14px;
  }
}

@media only screen and (max-width: 450px) {
  .movie-cover{
    width: 80%;
    margin: 0 auto;
  }

  header{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  header .logo{
    text-align: center;
    width: 100%;
    margin-bottom: 8px;
  }
  header form {
    width: 100%;
  }
  header form input{
    width: 100%;
  }
}
